.hero {
  padding: 30px;
  background-color: rgb(55, 176, 251);
  color: white;
}
.hero img {
  width: 40%;
  margin-left: 20px;
  margin-top: 70px;
}
.hero p {
  color: white;
  width: 90%;
  font-size: 12px;
  font-weight: bolder;
  text-align: center;
  margin: auto auto;
}

.hero h1 {
  margin-top: -50px;
  color: white;
  font-size: 30px;
  font-weight: bolder;
}

h3 {
  color: #30393e;
  font-size: 24px;
  margin-top: 80px;
  margin-bottom: -10px;
}

.skills {
  font-size: 12px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.skill {
  border: 0.5px solid gray;
  border-radius: 30px;
  padding: 10px;
  background-color: #f8f8f8;
  margin: 10px;
  font-weight: bold;
}
.skill h4 {
  font-size: 16px;
}
.skill li {
  list-style: none;
}
.skill img {
  width: 10%;
}
.connect {
  background-color: #30393e;
  color: whitesmoke;
  padding: 20px;
  min-height: 250px;
}
.connect img {
  width: 15%;
  margin-left: 20px;
}

a.body-link:link {
  color: whitesmoke;
}
a.body-link:visited {
  color: whitesmoke;
}
a.body-link:hover {
  color: lightskyblue;
  cursor: pointer;
}
a.body-link:active {
  color: rgb(244, 255, 35);
}
@media only screen and (min-width: 768px) {
  .hero {
    padding: 60px;
    font-weight: bold;
  }

  .hero h1 {
    font-size: 40px;
    color: white;
  }

  .hero p {
    font-size: 20px;
  }
  .hero img {
    margin-top: 50px;
    margin-bottom: -20px;
    width: 25%;
  }
  h3 {
    font-size: 28px;
  }
  .skills-header {
    font-size: 36px;
    margin-top: 40px;
  }
  .skills {
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .skill {
    font-size: 18px;
  }
  .skill h4 {
    font-size: 22px;
    font-weight: bolder;
  }

  .skill img {
    width: 8%;
  }
  .connect h2 {
    font-size: 36px;
  }
  .connect img {
    width: 10%;
    margin-left: 20px;
  }
}

@media only screen and (min-width: 1200px) {
  .hero {
    padding: 60px;
    font-weight: bold;
  }

  .hero h1 {
    font-size: 40px;
    color: white;
  }

  .hero p {
    font-size: 18px;
    width: 50%;
  }
  .hero img {
    margin-top: 50px;
    margin-bottom: -20px;
    width: 10%;
  }
  h3 {
    font-size: 48px;
  }
  .skills {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 65%;
    margin: 50px auto;
  }

  .skill {
    font-size: 16px;
  }
  .skill h4 {
    font-size: 22px;
    font-weight: bolder;
  }
  .skills-header {
    font-size: 36px;
  }
  .skill img {
    width: 10%;
  }
  .connect h2 {
    font-size: 36px;
    margin: 50px auto;
  }
  .connect img {
    width: 4%;
    margin-left: 20px;
    margin-bottom: 40px;
  }
}
