.portfolio {
  min-height: 100px;
  width: 95%;
  margin: 50px auto;
  padding: 40px;
}
.portfolio-container {
  border: 2px solid gray;
  margin-bottom: 75px;
  display: flex;
  flex-direction: column;
}
h3 {
  margin-top: 10px;
}
.screenshot {
  width: 100%;
  max-height: auto;
}
a.body-link:link {
  color: #30393e;
}
a.body-link:visited {
  color: #30393e;
}
a.body-link:hover {
  color: rgb(0, 157, 255);
  cursor: pointer;
}
a.body-link:active {
  color: rgb(244, 255, 35);
}
@media only screen and (min-width: 768px) {
  .portfolio h2 {
    font-size: 40px;
  }
}
@media only screen and (min-width: 1200px) {
  .portfolio h2 {
    font-size: 50px;
  }
  .portfolio {
    min-height: 100px;
    width: 40%;
    margin: 50px auto;
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 16px;
  }
  .portfolio-container {
    border: 2px solid gray;
    margin: 50px;
  }

  .screenshot {
    width: 100%;
    max-height: auto;
  }
  h3 {
    font-size: 24px;
    margin-top: 10px;
  }
}
