nav {
  display: flex;
  width: 100%;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: 0 10px;
  background-color: #f8f8f8;
  justify-content: space-evenly;
}

nav ul {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding-inline-start: 10;
  margin: 10px;
  text-align: center;
}

nav ul li {
  margin-left: 8px;
  line-height: 18px;
}

.logo {
  color: #30393e;
  text-decoration: none;
  transition-duration: 100ms;
  font-size: 18px;
  font-weight: bolder;

  margin: 15px 0;
}

.logo:hover {
  color: #0053b8;
  transition-duration: 500ms;
}

.nav-item {
  color: #30393e;
  text-decoration: none;
  transition-duration: 100ms;
  border-radius: 2px;
  font-size: 16px;
  font-weight: bolder;
}
nav ul li {
  margin-left: 8px;
  line-height: 35px;
}

.nav-item:hover {
  color: #0053b8;
  transition-duration: 500ms;
}

@media only screen and (min-width: 375px) {
  nav ul li {
    margin-left: 18px;
    line-height: 40px;
  }
  .logo {
    font-size: 22px;
  }
}

@media only screen and (min-width: 768px) {
  nav {
    height: 75px;
  }

  .logo {
    font-size: 30px;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-right: 150px;
  }

  .nav-item {
    font-size: 20px;
  }

  nav ul li {
    margin-top: 10px;
    margin-left: 50px;
  }
}

@media only screen and (min-width: 1200px) {
  nav ul li {
    margin-top: 10px;
    margin-left: 100px;
  }
}
