.connect-page {
  background-color: #30393e;
  background-size: cover;
  min-height: 100vh;
  color: whitesmoke;
  padding: 80px;
}
.connect-page img {
  width: 20%;
  margin-left: 20px;
}

.connect-page h2 {
  font-size: 36px;
}

@media only screen and (min-width: 768px) {
  .connect-page img {
    width: 13%;
    margin-left: 20px;
  }

  .connect-page h2 {
    margin-top: 100px;
    font-size: 56px;
  }
}
@media only screen and (min-width: 1200px) {
  .connect-page img {
    width: 5%;
    margin-left: 20px;
  }

  .connect-page h2 {
    margin-top: 100px;
    font-size: 56px;
  }
}
